
body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

a:link,
a:active,
a:visited,
a:hover {
  color: blue;
}

ul {
  margin: 0px;
  padding: 0px;
  text-decoration: none;
}

li {
  display: inline-block;
}

.container {
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}

.row {
  width: 100%;
  height: 100%;
}
